<template>
    <v-card>
        <v-toolbar color="primary" dark flat>
            <v-toolbar-title>Add/Edit Questions</v-toolbar-title>
            <v-spacer />
        </v-toolbar>
        <v-card-text>
            <v-slide-y-transition group>
            <v-form v-for="q in questions" :key="q.id">                
                <v-text-field placeholder="Enter Question Text" :label='"Question #"+(q.id+1)' v-model="q.text" filled rounded>
                    <template v-slot:append>
                        <v-btn text icon small @click="removequestion(q)">
                            <v-icon color="red">fas fa-times</v-icon>
                        </v-btn>
                    </template>
                </v-text-field>
                <v-slide-y-transition group>
                    <div v-for="c in q.choices" :key="c.id" class="pl-4" style="width:90%">
                        <v-text-field placeholder="Enter Choice Text" outlined dense v-model="c.text">
                            <template v-slot:prepend>
                                <v-btn text small icon @click="correct(c,q.choices)">
                                    <v-icon v-if="!c.correct" color="grey lighten-2">far fa-check-circle</v-icon>
                                    <v-icon v-if="c.correct" color="green lighten-2">fas fa-check-circle</v-icon>
                                </v-btn>
                            </template>
                            <template v-slot:append-outer>
                                <v-btn text icon small @click="remove(c,q.choices)">
                                    <v-icon color="red">fas fa-times-circle</v-icon>
                                </v-btn>
                            </template>
                        </v-text-field>
                    </div>
                </v-slide-y-transition>
                <div class="pl-4 mb-3">
                    <v-btn x-small color="warning" dark @click.prevent="addchoice(q)">+ Add Choice</v-btn>
                </div>
                <div class="pa-2"></div>
            </v-form>
            </v-slide-y-transition>
            <v-btn color="primary" class="mr-4" @click="addquestion()">ADD QUESTION</v-btn>                
            <v-btn color="success" class="mr-4" @click="submit">SUBMIT</v-btn>
        </v-card-text>
    </v-card>
</template>

<script>
export default {
    name:'QuestionMaker',
    data: () => ({
        questions: 
        [
            {
                "id": 0,
                "text": "How many sides does a hexagon have?",
                "choices": 
                [{
                    "id": 0,
                    "text": "4 sides",
                    "correct": false
                }, 
                {
                    "id": 1,
                    "text": "5 sides",
                    "correct": false
                },
                {
                    "id": 2,
                    "text": "6 sides",
                    "correct": true
                },
                {
                    "id": 3,
                    "text": "7 sides",
                    "correct": false
                }]
            }
        ]
    }),
    methods:{
        submit(){
            this.$emit('submit');
        },
        addchoice(q){
            let newid = 0;
            if(q.choices.length > 0 ){
                const highest = Math.max.apply(Math, q.choices.map(function(o) { return o.id; }))            
                newid = highest + 1;
            } 
            q.choices.push({"id":newid,"text":"","correct":false});
        },
        correct(c,choices){
            choices.forEach(el => {
                if(el.id === c.id){
                    el.correct = true;
                } else {
                    el.correct = false;
                }
            });
        },
        remove(c,choices){
            choices.forEach(function(item, index, object) {
                if (item.id === c.id) {
                    object.splice(index, 1);
                }
            });
        },
        removequestion(q){
            this.questions.forEach(function(item, index, object) {
                if (item.id === q.id) {
                    object.splice(index, 1);
                }
            });
        },
        addquestion(){
            let newid = 0;
            if(this.questions.length > 0 ){
                const highest = Math.max.apply(Math, this.questions.map(function(o) { return o.id; }))                      
                newid = highest + 1;
            } 
            this.questions.push({"id":newid,"text":"","choices":[]});            
        }
    }
}
</script>
